body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#ChipType_Peripheral {
  height: 11vh;
}

#MCU_Peripheral {
  height: 11vh;
}

#ChipType_Peripheral,
#MCU_Peripheral {
  padding-left: 15px;
  width: 210px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
#rootTree_Peripheral {
  padding-inline: 5px;
  width: 230px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

#CodeGeneratorContainer {
  width: -webkit-fill-available!important;
  height: 90vh;
  overflow: auto;
  position: absolute;
  left: 240px!important;
}

#panel {
  width: -webkit-fill-available!important;
  overflow: auto!important;
  height: 90vh!important;
}

#tabs {
  top: 64px!important;
}

/* [Workaround]  From  ~\PinConfigure\css\pepper-grinder\jquery-ui-1.10.3.custom.css */
.ui-tabs {
	position: relative;/* position: relative prevents IE scroll bug (element with position: relative inside container with overflow: auto appear as "fixed") */
	padding: .2em;
}
#generateCodeDialog .ui-tabs .ui-tabs-nav {
	margin: 0;
	padding: .2em .2em 0;
}
#generateCodeDialog .ui-tabs .ui-tabs-nav li {
	list-style: none;
	float: left;
	position: relative;
	top: 0;
	margin: 1px .2em 0 0;
	border-bottom-width: 0;
	padding: 0;
	white-space: nowrap;
	font-family: Arial;
	text-align: center;
}
/* [Workaround] End */